import { NextSeo } from 'next-seo';
import React, { useContext } from 'react';
import Tabber from '@components/common/Tabber';
import WinnerBonuses from '@components/account/Worlds/World6/summoning/WinnerBonuses';
import { AppContext } from '@components/common/context/AppProvider';
import Upgrades from '@components/account/Worlds/World6/summoning/Upgrades';
import { notateNumber } from '@utility/helpers';
import { CardTitleAndValue } from '@components/common/styles';
import { Divider, Stack } from '@mui/material';
import Battles from '@components/account/Worlds/World6/summoning/Battles';
const Summoning = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    winnerBonuses,
    upgrades,
    essences,
    allBattles,
    armyHealth,
    armyDamage,
    highestEndlessLevel
  } = state?.account?.summoning || {};
  return <>
    <NextSeo title="Summoning | Idleon Toolbox" description="Keep track of your summoning bonuses" data-sentry-element="NextSeo" data-sentry-source-file="summoning.jsx" />
    <Stack direction={'row'} gap={1} flexWrap={'wrap'} my={3} data-sentry-element="Stack" data-sentry-source-file="summoning.jsx">
      <CardTitleAndValue value={highestEndlessLevel} icon={'etc/Endless_Summoning.png'} imgStyle={{
        width: 25
      }} cardSx={{
        my: 0
      }} data-sentry-element="CardTitleAndValue" data-sentry-source-file="summoning.jsx" />
      <Divider sx={{
        borderRightWidth: 'medium'
      }} flexItem orientation={'vertical'} data-sentry-element="Divider" data-sentry-source-file="summoning.jsx" />
      {essences?.map((value, index) => {
        if (index > 6) return null;
        return <CardTitleAndValue key={index} value={notateNumber(value)} icon={`data/SummC${index + 1}.png`} cardSx={{
          my: 0
        }} />;
      })}
    </Stack>
    <Tabber tabs={['Upgrades', 'Winner Bonuses', 'Battles']} data-sentry-element="Tabber" data-sentry-source-file="summoning.jsx">
      <Upgrades upgrades={upgrades} data-sentry-element="Upgrades" data-sentry-source-file="summoning.jsx" />
      <WinnerBonuses winnerBonuses={winnerBonuses} data-sentry-element="WinnerBonuses" data-sentry-source-file="summoning.jsx" />
      <Battles battles={allBattles} armyHealth={armyHealth} armyDamage={armyDamage} highestEndlessLevel={highestEndlessLevel} data-sentry-element="Battles" data-sentry-source-file="summoning.jsx" />
    </Tabber>
  </>;
};
export default Summoning;